.carousel {
  border: 2px solid #800080;
  border-radius: 5px;
  margin-bottom: 10px; }

.carousel img {
  display: block;
  margin: 0 auto;
  -o-object-fit: cover;
     object-fit: cover;
  z-index: -1; }

.header__inner {
  border: 2px solid #800080;
  border-radius: 5px;
  background-color: #800080; }

.header__nav {
  margin-bottom: 10px; }

.header a,
.header span {
  color: #ffffff; }

.header li:hover a {
  color: #800080; }
